import {jsPDF} from "jspdf";
import html2canvas from "html2canvas";

// TODO Figure out how to format the PDF correctly
const CANVAS_ID = "download-canvas";
const DOWNLOAD_FILENAME = "letter-to-heirs.pdf";
const CANVAS_SELECTOR = '#' + CANVAS_ID;

const downloadPDF = () => {
    const el = document.querySelector('#document');
    const pdf = new jsPDF({
        format: 'letter',
    });
    let canvas;

    html2canvas(el)
        .then(c => {
            c.id = CANVAS_ID;
            document.body.appendChild(c);
            canvas = c;
        })
        .then(() => pdf.html(document.querySelector(CANVAS_SELECTOR)))
        .then(() => pdf.save(DOWNLOAD_FILENAME))
        .then(() => document.body.removeChild(canvas));
}

export default downloadPDF;
