import React, {useEffect, useState} from 'react';
import {Openlaw} from 'openlaw';
import Form from './Form';
import "./App.css";
import EndowlNavbar from "./EndowlNavBar";
import {debounce} from "lodash";

// The preview renderer needs to live outside of a component so that it isn't re-instantiated every time it is executed
const renderPreviewHTML = debounce((formState, callback) => {
    const {executionResult} = formState;
    const {agreement} = Openlaw.getAgreements(executionResult)[0];

    callback(Openlaw.renderForPreview(agreement, {}, {}));
}, 300);

const App = () => {
    const [formState, liftFormState] = useState();
    const [previewHTML, setPreviewHTML] = useState();
    const [view, toggleView] = useState('source');

    useEffect(() => {
        if (formState) {
            renderPreviewHTML(formState, setPreviewHTML);
            toggleView('preview');
        }
    }, [formState]);

    return (
        <>
            <EndowlNavbar />

            <div className="paneLeft">
                <Form
                    stateLifter={liftFormState}
                />
            </div>

            {/* PREVIEW */}
            <div className="document-view paper">
                {(previewHTML && view === 'preview') && (
                    <div id="document" dangerouslySetInnerHTML={{__html: previewHTML}}/>
                )}
            </div>
        </>
    );
};

export default App;
